* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

ul {
  box-sizing: border-box;
  padding: 0 1em;
}

footer {
  margin: 40px 0 20px;
  width: 100%;
  text-align: center;
}

button {
  position: relative;
  font-size: 1.2em;
  font-weight: 700;
  border: solid 1px black;
  border-radius: 4px;
  outline: none;
  background-color: rgba(206, 206, 206, 0.25);
  color: black;
  padding: 5px;
}
button::after {
  display: block;
  content: '';
  position: absolute;
  z-index: -1;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 0%;
  height: 100%;
  background-color: rgb(0, 0, 0);
  transition: width 0.15s ease-out;
}

button:hover::after {
  width: 101%;
  border-radius: 4px;
}
button:hover {
  cursor: pointer;
  color: aliceblue;
}
button:active {
  opacity: 0.35;
}
button:active::after {
  width: 101%;
}

#grade-list {
  display: flex;
  flex-direction: column;
  gap: 5px;
  width: 375px;
}

.subject-div {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.subject-div label {
  width: 65%;
}

.subject-div input {
  width: 30%;
  padding: 2px;
}

#god-father {
  display: flex;
  width: 100%;
  justify-content: center;
}

#actual-father {
  width: 375px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

#canvas {
  width: 40%;
  border: solid black 2px;
}

#canva-grandpa {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#canva-grandpa * {
  margin: auto;
}

.form-download {
  box-sizing: border-box;
  width: 240px;
}

@media (max-width: 1500px) {
  #canvas {
    width: 60%;
  }
}

@media (max-width: 767px) {
  #actual-father {
    width: 280px;
  }

  #grade-list {
    width: 280px;
  }

  #canvas {
    width: 80%;
  }
}